.card {
  background: #fff;
  display: 'flex';
  border-radius: 2px;
  /* height: 325px; */
  margin: 1rem;
  position: relative;
  width: 100%;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
  padding: 10px;
}

.card > .img-container {
  /* height: 150px;
  width: 220px; */
  overflow: auto;
  /* text-align: center; */
  /* background:#6CADDC; */
}
.card > .img-container > img {
 float: left;
 padding: 0px 15px 5px 5px;
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

.card > .content > ul {
  list-style-type: none;
}

.card > .content > ul > li {
  margin-top: 18px;
}

.card > .remove {
  color: #ff1744;
  font-size: 48px;
  position: absolute;
  top: -20px;
  right: 5px;
  cursor: pointer;
}

.card > .remove:hover {
  color: #d50000;
}
